<template>
<div>
  <!-- Image and text -->
  <b-navbar variant="faded" type="light" >
    <b-navbar-brand href="#">
       <img src ="../assets/tirhalLogo2.png" alt="Kitten" style = "width: 100px; hight: 100px;" position: sticky/>
    </b-navbar-brand>
<b-navbar-nav class="ml-auto">
        <div>
          <b-button style="background:#f05623; width:150px; margin-left:50px;"  href = "drivers" pill size="md" >Drivers</b-button>
          <b-button style="background:#f05623; width:150px;"  @click= "logOut"  pill size="md" >Log Out</b-button>
        </div>
        
</b-navbar-nav>
  </b-navbar>
   <b-container fluid>
    <!-- User Interface controls -->
    <b-row>
      <b-col lg="6" class="my-1">
      
      </b-col>

      <b-col lg="6" class="my-1">
        
      </b-col>

      <b-col lg="6" class="my-1">
        <b-form-group
          label="Filter"
          label-cols-sm="3"
          label-align-sm="right"
          label-size="sm"
          label-for="filterInput"
          class="mb-0"
        >
          <b-input-group size="sm">
            <b-form-input
              v-model="filter"
              type="search"
              id="filterInput"
              placeholder="Type to Search"
            ></b-form-input>
            <b-input-group-append>
              <b-button :disabled="!filter" @click="filter = ''">Clear</b-button>
            </b-input-group-append>
          </b-input-group>
        </b-form-group>
      </b-col>

      <b-col lg="6" class="my-1">
        <b-form-group
          label="Filter On"
          label-cols-sm="3"
          label-align-sm="right"
          label-size="sm"
          description="Leave all unchecked to filter on all data"
          class="mb-0">
          <b-form-checkbox-group v-model="filterOn" class="mt-1">
            <b-form-checkbox value="name">Name</b-form-checkbox>
            <b-form-checkbox value="true">isActive</b-form-checkbox>
             <b-form-checkbox value="true">isAdmin</b-form-checkbox>
          </b-form-checkbox-group>
        </b-form-group>
      </b-col>

    </b-row>

    <!-- Main table element -->
    <b-table
      show-empty
      small
      ref="table"
      :busy.sync="isBusy"
      :items="myProvider"

      :fields="fields"
      :current-page="currentPage"
      :per-page="perPage"
      :filter="filter"
      :filterIncludedFields="filterOn"
      @filtered="onFiltered"
    >
      <template v-slot:cell(name)="row">
        {{ row.value.first }} {{ row.value.last }}
      </template>

     <template v-slot:cell(actions)="row">
       <b-col>
             <b-icon icon="pencil-square" @click="updateUser(row)"  scale="2" ></b-icon>
             <b-icon icon="trash" @click="deleteUser(row)" scale="2" style ="margin-left:30px;">  scale="2" class="bg-info"></b-icon>
          <b-button size="sm" variant = "danger" style ="margin-left:30px" @click="updatePassword(row)"> Reset password 
        </b-button>
         </b-col>
       
      </template>

      <template v-slot:row-details="row">
        <b-card>
          <ul>
            <li v-for="(value, key) in row.item" :key="key">{{ key }}: {{ value }}</li>
          </ul>
        </b-card>
      </template>
    </b-table>
<b-row>
       
      <b-col sm="5" md="6" class="my-1">
          
         <b-button  variant="success" v-b-modal.modal-prevent-closing class="mr-1">
          Add User
        </b-button>
       
      </b-col>

      <b-col sm="7" md="6" class="my-1">
        <b-pagination
          v-model="currentPage"
          :total-rows="totalRows"
          :per-page="perPage"
          align="fill"
          size="sm"
          class="my-0"
        ></b-pagination>
      </b-col>
</b-row>

    <b-modal
      id="modal-prevent-closing3" hide-footer
      ref="modal"
      title="Reset user password"
      @show="resetModal"
      @hidden="resetModal"
    >
      <form @submit="onResetPassword"  @submit.stop.prevent  @hidden="resetModalReset">
         <b-form-group
          id="input-group-2" 
          label="Password" 
          label-for="input-2"   
          :invalid-feedback="invalidFeedback2"
          :valid-feedback="validFeedback2"
          :state="state2"   
          >
    <b-form-input    
    type="password" 
    id="text-password-2"  
    aria-describedby="password-help-block" 
    required
    placeholder="password"
    v-model="formReset.password1"
    :state="state2"   
     >     
     </b-form-input>
      </b-form-group>
       <b-form-group
          id="input-group" 
          label="Confirm Password" 
          label-for="input1"      
          :invalid-feedback="invalidFeedback"
          :valid-feedback="validFeedback"
          :state="state"
          >
    <b-form-input    
    type="password" 
    id="text-password2"  
    aria-describedby="password-help-block" 
    v-model="formReset.password"
    required
    placeholder="Confirm Password"
    :state="state"
     >  
    </b-form-input>
      </b-form-group>
       <input
            type= "hidden"
            v-model="formReset.id"
          /> 
         <input
            type= "hidden"
            v-model="formReset.UserName"
          />  
           <input
            type= "hidden"
            v-model="formReset.isActive"
          />   
          <input
            type= "hidden"
            v-model="formReset.isAdmin"
          />
      <center>
       <b-button id="mybtn" type="submit" style="width:250px" 
          pill block >save</b-button>
</center>

      </form>
    </b-modal>
    <b-modal
      id="modal-prevent-closing" hide-footer
      ref="modal"
      title="Add new user"
      @show="resetModal"
      @hidden="resetModal"
    >
      <form @submit="onSubmit"  @submit.stop.prevent  @hidden="resetModal">
        <b-form-group
          label="User Name"
          label-for="name-input"
          invalid-feedback="Name is required"
        >
          <b-form-input
            id="name-input"
            v-model="form.UserName"
            required
            placeholder="user  name"
          ></b-form-input>
        </b-form-group>
         <b-form-group
          id="input-group-2" 
          label="Password" 
          label-for="input-2"      
          invalid-feedback="password is required"
          >
    <b-form-input    
    type="password" 
    id="text-password"  
    aria-describedby="password-help-block" 
    v-model="form.password"
    required
    placeholder="password"
     >     
    </b-form-input>
      </b-form-group>
    <b-form-group>
      <b-form-checkbox-group>
        <b-form-checkbox id="checkbox-group-20"  value="true"  v-model="form.isActive">Active</b-form-checkbox>
          </b-form-checkbox-group>
           <b-form-checkbox-group>
         <b-form-checkbox id="checkbox-group-33"   value="true"  v-model="form.isAdmin">Admin</b-form-checkbox>
      </b-form-checkbox-group>
    </b-form-group>

<center>
       <b-button id="mybtn" type="submit" style="width:250px" 
          pill block >save</b-button>
</center>

      </form>
    </b-modal>
     <b-modal
      id="modal-prevent-closing2" hide-footer
      ref="modal"
      title="Update user info"
      @show="resetModal"
      @hidden="resetModal"
    >
    <form @submit="onSaveUpdate"  @submit.stop.prevent @hidden="resetModal">
       
        <b-form-group
          label="User Name"
          label-for="name-input"
          invalid-feedback="Name is required"
        >
          <b-form-input
            id="name-input"
            v-model="formUdate.UserName"
            required
            placeholder="user  name"
          ></b-form-input>
  
        </b-form-group>
        <b-form-group>
     <label>is user Admin?</label>
      <b-form-radio  v-model="formUdate.isAdmin" name="some-radios2" value="True">Yes</b-form-radio>
               <b-form-radio  v-model="formUdate.isAdmin" name="some-radios3" value="false">No</b-form-radio>
</b-form-group>
    <b-form-group>
    <label>is user Active?</label>
   <b-form-radio v-model="formUdate.isActive" name="some-radios" value="true">Yes</b-form-radio>
         <b-form-radio  v-model="formUdate.isActive" name="some-radios1" value="false">No</b-form-radio>
    </b-form-group>
    

    
          <input
            type= "hidden"
            v-model="formUdate.id"
          />

      <b-row>
        <b-col>
     
          <b-button id="mybtn3" type="Save" size = "sm"  
          pill block @click: onSaveUpdate>save</b-button>
          </b-col>
      </b-row>
    
    </form>
     </b-modal>
<div>
   <b-overlay :show="busy" rounded="lg" opacity="0.6">
      <template v-slot:overlay>
        <div class="d-flex align-items-center">
          <b-spinner small type="grow" variant="secondary"></b-spinner>
          <b-spinner type="grow" variant="dark"></b-spinner>
          <b-spinner small type="grow" variant="secondary"></b-spinner>
          <!-- We add an SR only text for screen readers -->
          <span class="sr-only">Please wait...</span>
        </div>
        </template>
   </b-overlay>
        </div>
     
     <div>
 <!-- The modal -->
 
        
 
  <b-modal id="my-modal" 
    hide-footer
    @delete = "deleteUser"
  >
  are you sure you want to delete user?
  <b-row>
    <b-col>
    <b-button size="sm" id="mybtn" type="delete" variant="danger"  @click= deleteUser> Delete 
        </b-button>
          <b-button size="sm" id="mybtn"  style="margin-left:10px;"> cancel 
        </b-button>
    </b-col>
  </b-row>
        </b-modal>
 </div>
  </b-container>
</div>
</template>

<script>
import axios from "axios";
import store from "@/store";
import VueRouter from 'vue-router';
  export default {
    data() {
      return {

        formUdate: {
          id:'',
          UserName: '',
          isActive: false,
          isAdmin:  false,
          selected: []
          },

          formReset: {
          id:'',
          UserName: '',
          password1: '',
          password: '',
          isActive: false,
          isAdmin:  false,

          },

        form: {
          UserName: '',
          password:'',
          isActive: false,
          isAdmin:  false
        },
        show: false,
      
        name: '',
        items: [],
        fields: [
          { key: 'userName', label: 'User Name', sortable: true},
          {
            key: 'isAdmin',
            label: 'is Admin',
            formatter: (value, key, item) => {
              return value ? 'Yes' : 'No'
            },
            sortable: true,
            sortByFormatted: true,
            filterByFormatted: true
          },
          {
            key: 'isActive',
            label: 'is Active',
            formatter: (value, key, item) => {
              return value ? 'Yes' : 'No'
            },
            sortable: true,
            sortByFormatted: true,
            filterByFormatted: true
          },
          { key: 'actions', label: 'Actions' }
        ],
        totalRows: 1,
        currentPage: 1,
        perPage: 5,
        sortBy: '',
        isBusy: false,
        filter: null,
        filterOn: [],
        options: [
          { text: 'Admin', value: 'isAdmin' },
          { text: 'Active', value: 'isActive' },
        
        ],
         busy: false,
        timeout: null
      }
      
    },

    computed: {
         //...mapGetters(['users']),
      sortOptions() {
        // Create an options list from our fields
        return this.fields
          .filter(f => f.sortable)
          .map(f => {
            return { text: f.label, value: f.key }
          })
      },
       state() {
        return (this.formReset.password.length >= 4 && this.formReset.password == this.formReset.password1) ? true : false
      },
      invalidFeedback() {
        if (this.formReset.password != this.formReset.password1) {
          return 'password is not matching'
        }
        
        else {
          return 'Please enter something'
        }
      },
       validFeedback() {
        return this.state === true ? 'looks good' : ''
      },
         state2() {
        return this.formReset.password1.length >= 4 ? true : false
      },
      invalidFeedback2() {
        if (this.formReset.password1.length <= 4 ) {
          return 'password must be at least 4 charecters, you can use letters, numbers and symbols'
        }
        else {
 return this.state2 === true ? 'valid Password' : ''        }
      },
       validFeedback2() {
        return this.state2 === true ? 'valid Password' : ''
      },
    },
    
    methods: {
        logOut() {
        store.user = null;
        this.$router.push ("/")
    },
        makeToast(variant = "null", massage = "null") {
        
        this.$bvToast.toast( massage, {
          title: ` ${variant || 'Message'}`,
          variant: variant,
          solid: true
        })
      
         this.onOk();
      },
       onOk() {
          
     this.$refs.table.refresh();
     this.resetModal();
    },


 onSubmit(evt) {
        evt.preventDefault()
       // this.handleSubmit()
        var form_data = new FormData()
        for(var key in this.form) {
        form_data.append(key, this.form[key])
        }

        console.log(form_data)

        this.busy = true
       axios.post('http://localhost:8081/add', form_data)
        .then( response=> {
          console.log(response)
         this.busy = false
         
         if (response.data == "user Saved") {
           this.makeToast("success", "user added successfully!");
            this.resetModal()
         } else {
          this.makeToastdanger("danger","Something went wrong! user not added");
           this.resetModal()
           
         }
 
        })
        .catch( error=> {
          this.busy = false
          console.log(error)
         // alert(error)
        });
    },
      noteMsg(color, msg) {
        this.$bvToast.toast(msg, {
          title: 'System message',
          variant: color,
          solid: true
        })
      },
      toFormData(obj) {
        var form_data = new FormData()
        for(var key in obj) {
          form_data.append(key, obj[key])
        }
        return form_data
      },
myProvider (ctx) {
  
        //console.log(ctx)

         this.isBusy = true
        let promise = axios.get("http://localhost:8081/users?page="+ctx.currentPage+"&pagesize="+ctx.perPage+"&filter="+ ctx.filter )

        return promise.then((data) => {
            console.log(data);
          const items = data.data.items
            this.totalRows = data.data.pagination.total;
           // this.count= data.data.pagination.count;
           // this.perPage = data.data.pagination.per_page;
           // this.currentPage= data.data.pagination.current_page;
           // this.total_pages = data.data.pagination.total_pages;

          // Here we could override the busy state, setting isBusy to false
           this.isBusy = false
          return(items)
        }).catch(error => {
             console.log(error);
          // Here we could override the busy state, setting isBusy to false
           this.isBusy = false
          // Returning an empty array, allows table to correctly handle
          // internal busy state in case of error
          return []
        })
      },



      onFiltered(filteredItems) {
        // Trigger pagination to update the number of buttons/pages due to filtering
     
   
      //  this.currentPage = 1
      },
      resetModal() {
        this.form.UserName = ''
         this.form.password = ''
           this.$nextTick(() => {
          this.$bvModal.hide('modal-prevent-closing')
        })
      },
        resetModalUpate() {
        this.form.Driver_name = ''
         this.form.Driver_id = ''
         this.form.phoneNum = ''
         this.form.SimNum = ''
           this.$nextTick(() => {
          this.$bvModal.hide('modal-prevent-closing2')
        })
      },
        resetModalReset() {
        this.password = ''
        this.formReset.password1 = ''
         this.formReset.password = ''
           this.$nextTick(() => {
          this.$bvModal.hide('modal-prevent-closing3')
        })
      },
   updateUser:  function(row){

      this.formUdate.id = row.item.id
      this.formUdate.UserName = row.item.userName
      this.formUdate.isAdmin = row.item.isAdmin
      this.formUdate.isActive = row.item.isActive


     this.$bvModal.show('modal-prevent-closing2')
   },
     
     onSaveUpdate(evt) {
        evt.preventDefault()
       // this.handleSubmit()
        var form_data = new FormData()
        for(var key in this.formUdate) {
        form_data.append(key, this.formUdate[key])


        }


        this.busy = true
       axios.put("http://localhost:8081//update", form_data)
        .then( response=> {
          console.log(response)
         this.busy = false
         
         if (response.data == "Record updated successfully") {
           this.makeToast("success", "Record updated successfully");
            this.resetModalUpate()
         } else {
          this.makeToastdanger("danger","Something went wrong! user info does not update");
           this.resetModalUpate()
           
         }
 
        })
        .catch( error=> {
          this.busy = false
          console.log(error)
         // alert(error)
        });
    },
       updatePassword:  function(row){

      this.formReset.id = row.item.id
      this.formReset.UserName = row.item.userName
      this.formReset.isAdmin = row.item.isAdmin
      this.formReset.isActive = row.item.isActive


     this.$bvModal.show('modal-prevent-closing3')
   },
     onResetPassword(evt) {
        evt.preventDefault()
       // this.handleSubmit() 
       
        var form_data = new FormData()
        for(var key in this.formReset) {
        form_data.append(key, this.formReset[key])
        

        }

       if (this.state === true){
        this.busy = true
       axios.put("http://localhost:8081//reset", form_data)
        .then( response=> {
          console.log(response)
         this.busy = false
         
         if (response.data == "password updated successfully") {
           this.makeToast("success", "password reset successfully");
            this.resetModalReset()
         } else {
          this.makeToastdanger("danger","Something went wrong! user info does not update");
           this.resetModalReset()
         
         }
 
        })
       
        .catch( error=> {
          this.busy = false
          console.log(error)
         // alert(error)
        });
       }
    },
      noteMsg(color, msg) {
        this.$bvToast.toast(msg, {
          title: 'System message',
          variant: color,
          solid: true
        })
      },
     deleteConfirmation (row) {
        
             this.$bvModal.show('my-modal')

      },
      toFormData(obj) {
        var form_data = new FormData()
        for(var key in obj) {
          form_data.append(key, obj[key])
        }
        return form_data
      },

         
 
  /*  onCancel() {
this.$bvModal.hide('my-modal')    
  }, */
  getRoles: function () {
                axios.get("http://localhost:8081/all")
                    .then(response => (this.items = response.data))
            },
   
  deleteUser: async function (role) {

if(confirm("are you sure you want to delete "+role.item.userName+"?")){
      this.busy = true
 await axios.delete("http://localhost:8081/deleteUser?Id=" + role.item.id)
   .then( response=> {
          console.log(response)
         this.busy = false
          
         if (response.data == ("user was deleted with id:" + role.item.id) ){
           this.makeToast("success", "user deleted successfully!");
           // resetModal()
         } else {
          this.makeToastdanger("danger", "Something went wrong! user not deleted");
           //this.resetModal()
           
         }
 
        })
        .catch( error=> {
          this.busy = false
          console.log(error)
         // alert(error)
        }); 
  

}
               
        }
     
    }
    
  }
</script>
